* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-1200 {
  width: 40px;
  height: auto;
  display: block;
}

.imgLogo img {
  width: 40px;
  display: block;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  justify-content: center;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  color: #00acdc;
  text-decoration: underline;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
